import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../ClientSideDataTable'
import PendingQuotaList from './PendingQuotaList'
import MyPendingQuotaList from './MyPendingQuotaList'

const QuotaList = () => {
    const AssignedMenus = JSON.parse(localStorage.getItem('AssignedMenus')) || []
    const Array2D = AssignedMenus.map(x => x.submenu.map(y => y.id))
    const subMenuIDs = [].concat(...Array2D)

    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [quotaList, setQuotaList] = useState([])
    const [myPendingQuotaList, setMyPendingQuotaList] = useState([])
    const [pendingQuotaList, setPendingQuotaList] = useState([])

    const user = JSON.parse(localStorage.getItem('userData'))
    useEffect(() => {
        useJwt.getQuotaList().then(res => {
            console.log(res)
            const allQuotas = []
            const allPendingQuotas = []
            const myPendingQuotas = []
            for (const q of res.data.payload) {
                if (q.is_approved === true || q.is_rejected === true) {
                    allQuotas.push(q)
                } else if (q.is_approved === false && q.is_rejected === false && parseInt(q.created_by) === user.id) {
                    myPendingQuotas.push(q)
                } else if (q.is_approved === false && q.is_rejected === false && parseInt(q.created_by) !== user.id) {
                    allPendingQuotas.push(q)
                }
            }
            setQuotaList(allQuotas)
            setMyPendingQuotaList(myPendingQuotas)
            setPendingQuotaList(allPendingQuotas)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
        })
    }, [resetData])
    const handlePoPupActions = (id, status, message) => {
        
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                const data = {
                    quota_id: id,
                    action_id: status,
                    reject_msg : ""
                }
                return useJwt.quotaApproveRejectDelete(data).then(res => {
                    Success(res)
                    setQuotaList(quotaList.filter(x => x.id !== id))
                    console.log(res)
                }).catch(err => {
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'SL',
            minWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Created By',
            minWidth: '100px',
            sortable: true,
            selector: 'created_by_name'
        },
        {
            name: 'User Type',
            minWidth: '100px',
            sortable: true,
            selector: Row => {
                return Row.created_user_type === 1 ? 'Admin' : 'Merchant'
            }
        },
        {
            name: 'Approved or Rejected',
            minWidth: '170px',
            sortable: true,
            selector: Row => {
                return Row.is_approved ? 'Approved' : 'Rejected'
            }
        },
        {
            name: 'Approved or Rejected by',
            minWidth: '170px',
            sortable: true,
            selector: 'approved_by'
        },
        {
            name: 'Facebook',
            minWidth: '100px',
            sortable: true,
            selector: 'facebook'
        },
        {
            name: 'Google',
            minWidth: '100px',
            sortable: true,
            selector: 'google'
        },
        {
            name: 'Whatsapp',
            minWidth: '100px',
            sortable: true,
            selector: 'whatsapp'
        },
        {
            name: 'SMS',
            minWidth: '100px',
            sortable: true,
            selector: 'sms'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'email'
        },
        {
            name: 'Push Notification',
            minWidth: '100px',
            sortable: true,
            selector: 'push_notification'
        },
        {
            name: 'Instagram',
            minWidth: '100px',
            sortable: true,
            selector: 'instagram'
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    {subMenuIDs.includes(32) && <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 3, 'Do you want to delete?')}
                        />
                    </span>}
                </>
            }
        }
    ]
    const [activeTab, setActiveTab] = useState('1')

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

    return (
        <Card>
          <CardBody className='pt-2'>
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                  <span className='align-middle d-none d-sm-block'>Quotas</span>
                </NavLink>
              </NavItem>
              {subMenuIDs.includes(33) && <NavItem>
                <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                  <span className='align-middle d-none d-sm-block'>My Pending Quotas</span>
                </NavLink>
              </NavItem>}
              {subMenuIDs.includes(34) && <NavItem>
                <NavLink active={activeTab === '3'} onClick={() => toggle('3')}>
                  <span className='align-middle d-none d-sm-block'>Approve Quotas</span>
                </NavLink>
              </NavItem>}
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId='1'>
                <Card>
                    <CardHeader className='border-bottom'>
                        <CardTitle tag='h4'>Quotas</CardTitle>
                        {subMenuIDs.includes(31) && <Button.Ripple className='ml-2' color='primary' tag={Link} to='/createQuota' >
                        <div className='d-flex align-items-center'>
                                <Plus size={17} style={{marginRight:'5px'}}/>
                                <span >Create Quota</span>
                        </div>
                        </Button.Ripple>}
                    </CardHeader>
                    <CardBody>
                        <Row>
                            <Col md='12'>
                                <CommonDataTable column={column} TableData={quotaList} TableDataLoading={TableDataLoading} />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
              </TabPane>
              <TabPane tabId='2'>
                <MyPendingQuotaList myPendingQuotaList={myPendingQuotaList} />
              </TabPane>
              <TabPane tabId='3'>
                <PendingQuotaList pendingQuotaList={pendingQuotaList} setPendingQuotaList={setPendingQuotaList} />
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
    )
}

export default QuotaList