import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, XSquare, CheckSquare, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../ClientSideDataTable'

const PendingQuotaList = ({pendingQuotaList, setPendingQuotaList}) => {
    const [TableDataLoading, setTableDataLoading] = useState(false)
    const [resetData, setReset] = useState(true)

    const handlePoPupActions = (id, status, message) => {
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                const data = {
                    quota_id: id,
                    action_id: status,
                    reject_msg : ""
                }
                return useJwt.quotaApproveRejectDelete(data).then(res => {
                    Success(res)
                    console.log(res)
                    setPendingQuotaList(pendingQuotaList.filter(x => x.id !== id))
                }).catch(err => {
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'SL',
            minWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Created By',
            minWidth: '100px',
            sortable: true,
            selector: 'created_by_name'
        },
        {
            name: 'User Type',
            minWidth: '100px',
            sortable: true,
            selector: Row => {
                return Row.created_user_type === 1 ? 'Admin' : 'Merchant'
            }
        },
        {
            name: 'Facebook',
            minWidth: '100px',
            sortable: true,
            selector: 'facebook'
        },
        {
            name: 'Google',
            minWidth: '100px',
            sortable: true,
            selector: 'google'
        },
        {
            name: 'Whatsapp',
            minWidth: '100px',
            sortable: true,
            selector: 'whatsapp'
        },
        {
            name: 'SMS',
            minWidth: '100px',
            sortable: true,
            selector: 'sms'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'email'
        },
        {
            name: 'Push Notification',
            minWidth: '100px',
            sortable: true,
            selector: 'push_notification'
        },
        {
            name: 'Instagram',
            minWidth: '100px',
            sortable: true,
            selector: 'instagram'
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Approve">
                        <CheckSquare size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 1, 'Do you want to approve?')}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Reject">
                        <XSquare size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 2, 'Do you want to reject?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Pending Services</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={pendingQuotaList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
            </Card>
    )
}

export default PendingQuotaList